.contact-juridical {
	padding: 80px 0;

	&__razdel {
		font-size: 16px;
		line-height: 120%;
		color: #EF4949;
	}

	&__zag {
		font-weight: 600;
		font-size: 22px;
		line-height: 200%;
		color: #232323;
		margin-bottom: 15px;
	}

	&__container {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;

		> p {
			width: 45%;
			font-size: 16px;
			line-height: 156%;
			color: #363636;
			padding-top: 50px;

		}
	}

	&__list {
		width: 45%;

		ul {
			display: flex;
			flex-wrap: wrap;
			margin-top: 20px;
			justify-content: space-between;

			li {
				width: 45%;

				&:nth-child(even) {
					text-align: right;
				}

				.red {
					font-size: 16px;
					line-height: 212%;
					color: #F74B4B;
					min-width: 80px;
					display: inline-block;
				}
			}
		}
	}

	.zag {
		font-weight: 600;
		font-size: 16px;
		line-height: 156%;
		letter-spacing: -0.02em;
		color: #2B2B2B;

	}
}

.contact-juridical.with-img {
	.contact-juridical__container > p{
		padding-top: 0;
	}
	.contact-juridical__list {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		ul{
			margin-top: 5px;
		}
		.img {
			width: 200px;
			height: 200px;
			position: relative;
			padding: 5px;
			border-radius: 50%;
			border: 5px solid rgba(255, 255, 255, 0.16);
			img {
				width: calc(100% + 10px);
				max-width: calc(100% + 10px);
				height: calc(100% + 10px);
				position: absolute;
				left: -5px;
				top: -5px;
				border-radius: 50%;
				z-index: -1;
			}
		}
		.text{
			width: calc(100% - 240px);
		}
		li{
			width: 100%;
			text-align: left;
			margin-bottom: 5px;
		}
		.red{
			margin-left: 20px;
		}

	}
}